/**
 * 
 * @param id
 * @return promise
 */
export let getPrice = (id) => {
    return sendRequest(`/v2-dealer-alllist.html?carId=${id}&cityId=201&_1539317031461`)
}

/**
 * 
 * @param {*} serialId id
 * @param {*} imgId id
 * @param {*} page 
 * @param {*} pageSize 
 * @param {*} carId id
 * @param {*} colorId Id
 */
export let getCategoryImgList = (param) => {
    let search = ``;
    for (let i in param) {
        search += `&${i}=${param[i]}`;
    }
    // pagepageSize
    if (!param['Page']) {
        search += `&Page=1`;
    }
    if (!param['PageSize']) {
        search += `&PageSize=30`;
    }
    return sendRequest(`/v2-car-getCategoryImageList.html?${search.slice(1, search.length)}`);
}

